<template>
  <div>
    <el-row style="margin-bottom: 20px;">
      <el-col :span="24">
        <el-button v-if="$menuPermission.HAS_ROLE_CREATE_BUTTON" type="primary" @click="createAction()"><i class="el-icon-apple el-icon--right"></i></el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-table
          :data="rolesData"
          border
          style="width: 100%">
          <el-table-column
            fixed
            prop="name"
            label=""
            width="200">
          </el-table-column>
          <el-table-column
            fixed
            prop="code"
            label=""
            width="200">
          </el-table-column>
          <el-table-column
            prop="description"
            label="">
          </el-table-column>
          <el-table-column
            fixed="right"
            label=""
            width="200">
            <template slot-scope="scope">
              <el-button v-if="$menuPermission.HAS_ROLE_UPDATE_BUTTON" @click="editAction(scope.row)" type="text" size="small"></el-button>
              <el-button v-if="$menuPermission.HAS_ROLE_DELETE_BUTTON" @click="deleteAction(scope.row)" type="text" size="small"></el-button>
              <el-button v-if="$menuPermission.HAS_ROLE_PERMISSION_BUTTON" @click="permissionAction(scope.row)" type="text" size="small"></el-button>
              <el-button v-if="$menuPermission.HAS_ROLE_MENU_BUTTON" @click="menuAction(scope.row)" type="text" size="small"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <!--  -->
    <el-dialog title="" :visible.sync="createDialogFormVisible">
      <el-form :model="roleForm" :rules="rules" ref="ruleForm" label-width="120px">
        <el-form-item label=""  prop="name">
          <el-input v-model="roleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="code">
          <el-input v-model="roleForm.code"></el-input>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input v-model="roleForm.description"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="createDialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="createRoleAction('ruleForm')"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogFormVisible">
      <el-form :model="roleForm" :rules="rules" ref="ruleForm" label-width="120px">
        <el-form-item label=""  prop="name">
          <el-input v-model="roleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="code">
          <el-input v-model="roleForm.code"></el-input>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input v-model="roleForm.description"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="createDialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="editRoleAction('ruleForm')"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="permissionDialogFormVisible">
      <el-transfer 
        v-model="permissions" 
        :titles="['', '']"
        :button-texts="['', '']"
        :data="permissionsData"></el-transfer>
      <div slot="footer" class="dialog-footer">
        <el-button @click="permissionDialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="managePermissionAction()"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="menuDialogFormVisible">
      <el-tree
        :data="menusData"
        show-checkbox
        node-key="id"
        ref="tree"
        :default-expanded-keys="[]"
        :default-checked-keys="defaultCheckedMenus"
        :props="defaultProps">
      </el-tree>
      <div slot="footer" class="dialog-footer">
        <el-button @click="menuDialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="manageMenuAction()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rolesData: [],
      createDialogFormVisible: false,
      editDialogFormVisible: false,
      permissionDialogFormVisible: false,
      menuDialogFormVisible: false,
      tapRoleId: 0,
      permissionsData:[],
      permissions:[],
      roleForm: {
        name: '',
        code: '',
        description: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '', trigger: 'blur' }
        ],
        description: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      menusData: [],
      defaultCheckedMenus: [],
      defaultProps: {
        children: 'children',
        label: 'name'
      }
    }
  },
  methods: {
    createAction() {
      this.roleForm.code = ''
      this.roleForm.name = ''
      this.roleForm.description = ''
      this.createDialogFormVisible = true
    },
    editAction(row) {
      this.roleForm.code = row.code
      this.roleForm.name = row.name
      this.roleForm.description = row.description
      this.editDialogFormVisible = true
      this.tapRoleId = row.id
    },
    deleteAction(row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.deleteRoleApi(row.id)
      }).catch(() => {
      })
    },
    permissionAction(row) {
      this.tapRoleId = row.id
      this.permissionDialogFormVisible = true
      this.permissionsApi()
    },
    menuAction(row) {
      this.tapRoleId = row.id
      this.menuDialogFormVisible = true
      this.menusApi()
    },
    createRoleAction(formName) {
      this.createDialogFormVisible = false
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.createRoleApi()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    editRoleAction(formName) {
      this.editDialogFormVisible = false
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.updateRoleApi(this.tapRoleId)
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    managePermissionAction() {
      this.permissionDialogFormVisible = false
      this.allotPermissionsApi(this.tapRoleId)
    },
    manageMenuAction() {
      this.menuDialogFormVisible = false
      var menuIdList = this.$refs.tree.getCheckedNodes(false, true).map(x => x.id)
      console.log(menuIdList);
      this.allotMenusApi(this.tapRoleId, menuIdList)
    },
    rolesApi: async function () {
      const res = await this.$http.get(this.$http.API.ROLES_ALL)
      if (res.success) {
        this.rolesData = res.data
      }
    },
    permissionsApi: async function () {
      this.permissionsData = []
      const res = await this.$http.get(this.$http.API.PERMISSIONS_ALL)
      if (res.success) {
        var list = res.data
        list.forEach(element => {
          var item = {
            key: element.id,
            label: element.name,
            disabled: false
          }
          this.permissionsData.push(item)
        })
        this.permissionsOfRoleApi(this.tapRoleId)
      }
    },
    menusApi: async function () {
      this.permissionsData = []
      const res = await this.$http.get(this.$http.API.MENUS_ALL)
      if (res.success) {
        this.menusData = res.data
        this.menusOfRoleApi(this.tapRoleId)
      }
    },
    permissionsOfRoleApi: async function (roleId) {
      this.permissions = []
      const res = await this.$http.get("/system/role/" + roleId + "/permissions")
      if (res.success) {
        var list = res.data
        list.forEach(element => {
          this.permissions.push(element.id)
        })
      }
    },
    menusOfRoleApi: async function (roleId) {
      this.defaultCheckedMenus = []
      const res = await this.$http.get("/system/role/" + roleId + "/menus")
      if (res.success) {
        var list = res.data
        //  id
        var checked = []
        list.forEach(element => {
          checked.push(element.id)
        })
        this.defaultCheckedMenus = checked
        console.log('defaultCheckedMenus:' + this.defaultCheckedMenus)
      }
    },
    allotPermissionsApi: async function (roleId) {
      const res = await this.$http.put("/system/role/" + roleId + "/permissions", this.permissions)
      if (res.success) {
        this.$message({
          message: '',
          type: 'success'
        })
      }
    },
    allotMenusApi: async function (roleId, menuIdList) {
      const res = await this.$http.put("/system/role/" + roleId + "/menus", menuIdList)
      if (res.success) {
        this.$message({
          message: '',
          type: 'success'
        })
      }
    },
    createRoleApi: async function () {
      let params = this.roleForm
      const res = await this.$http.post(this.$http.API.ROLES_CREATE, params)
      if (res.success) {
        this.$message({
          message: '',
          type: 'success'
        })
        this.rolesApi()
      }
    },
    updateRoleApi: async function (roleId) {
      let params = this.roleForm
      const res = await this.$http.put(this.$http.API.ROLES_UPDATE+roleId, params)
      if (res.success) {
        this.$message({
          message: '',
          type: 'success'
        })
        this.rolesApi()
      }
    },
    deleteRoleApi: async function (roleId) {
      const res = await this.$http.delete(this.$http.API.ROLES_DELETE+roleId)
      if (res.success) {
        this.$message({
          message: '',
          type: 'success'
        })
        this.rolesApi()
      }
    },
  },
  created() {
    this.rolesApi()
  }
}
</script>

