<template>
  <div>
    <el-row>
        <el-col :span="9"><div class="placeholder_hiden">1</div></el-col>
        <el-col :span="6">
            <div style="margin-top: 100px;">
                <h3 style="text-align:center;">OCat Beta 0.0.1</h3>
                <el-form :rules="rules" ref="ruleForm" :model="ruleForm">
                    <el-form-item prop="username">
                        <el-input prefix-icon='el-icon-user' v-model="ruleForm.username" placeholder="" clearable></el-input>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input prefix-icon='el-icon-view' v-model="ruleForm.password" placeholder="" type="password" @change="loginAction('ruleForm')" clearable></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="loginAction('ruleForm')" size="medium" style="width: 100%"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-col>
        <el-col :span="9"><div class="placeholder_hiden">1</div></el-col>
    </el-row>
  </div>
</template>

<script>

export default {
  data () {
    return {
      ruleForm: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  mounted () {
    if (localStorage.username) {
      this.ruleForm.username = localStorage.username
    }
  },
  methods: {
    loginAction (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loginApi()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    loginApi: async function () {
      let params = this.ruleForm
      const res = await this.$http.post(this.$http.API.LOGIN, params)
      if (res.success === true) {
        if (res.data.enabled === 2) {
          this.$message({
            message: '',
            type: 'warning'
          })
        } else {
          this.$message({
            message: '',
            type: 'success'
          })
          localStorage.username = this.ruleForm.username
          this.$store.commit('setUserInfo', res.data)
          console.log('localstore username:' + localStorage.username)
          console.log('' + this.$store.getters.loginInfo)
          console.log('' + this.$store.getters.isLogin)
          this.$menuPermission.checkPermissions()
          console.log('1'+this.$store.getters.hasButtonById('12'))
          console.log('2'+this.$menuPermission.HAS_USER_CREATE_BUTTON)
          this.$router.push('/home')
        }
      }
    }
  }
}
</script>

<style scoped>
.placeholder_hiden {
  visibility: hidden;
}
</style>
