<template>
  <div>
    <el-row style="margin-bottom: 20px;">
      <el-col :span="24">
        <el-button v-if="$menuPermission.HAS_APP_CREATE_BUTTON" type="primary" @click="createAction()"><i class="el-icon-apple el-icon--right"></i></el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-table
          :data="appsData"
          border
          style="width: 100%">
          <el-table-column
            fixed
            prop="name"
            label=""
            width="200">
          </el-table-column>
          <el-table-column
            fixed
            prop="appId"
            label="AppId"
            width="200">
          </el-table-column>
          <el-table-column
            fixed
            prop="appSecret"
            label="AppSecret"
            width="200">
          </el-table-column>
          <el-table-column
            prop="description"
            label="">
          </el-table-column>
          <el-table-column
            fixed="right"
            label=""
            width="200">
            <template slot-scope="scope">
              <el-button v-if="$menuPermission.HAS_APP_UPDATE_BUTTON" @click="editAction(scope.row)" type="text" size="small"></el-button>
              <el-button v-if="$menuPermission.HAS_APP_DELETE_BUTTON" @click="deleteAction(scope.row)" type="text" size="small"></el-button>
              <el-button v-if="$menuPermission.HAS_APP_PACKAGE_BUTTON" @click="gotoResourceAction(scope.row)" type="text" size="small"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <!--  -->
    <el-dialog title="" :visible.sync="createDialogFormVisible">
      <el-form :model="appForm" :rules="rules" ref="ruleForm" label-width="120px">
        <el-form-item label=""  prop="name">
          <el-input v-model="appForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input v-model="appForm.description"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="createDialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="createAppAction('ruleForm')"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogFormVisible">
      <el-form :model="appForm" :rules="rules" ref="ruleForm" label-width="120px">
        <el-form-item label=""  prop="name">
          <el-input v-model="appForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input v-model="appForm.description"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editDialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="editAppAction('ruleForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      appsData: [],
      createDialogFormVisible: false,
      editDialogFormVisible: false,
      tapAppId: 0,
      appForm: {
        name: '',
        description: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        description: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    createAction () {
      this.appForm.name = ''
      this.appForm.appId = ''
      this.appForm.appSecret = ''
      this.appForm.description = ''
      this.createDialogFormVisible = true
    },
    editAction (row) {
      this.appForm.name = row.name
      this.appForm.appId = row.appId
      this.appForm.appSecret = row.appSecret
      this.appForm.description = row.description
      this.editDialogFormVisible = true
      this.tapAppId = row.id
    },
    gotoResourceAction (row) {
      this.$router.push({ name: 'resources', query: { appInfo: row } })
    },
    deleteAction (row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.deleteAppApi(row.id)
      }).catch(() => {
      })
    },
    createAppAction (formName) {
      this.createDialogFormVisible = false
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.createAppApi()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    editAppAction (formName) {
      this.editDialogFormVisible = false
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.updateAppApi(this.tapAppId)
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    appsApi: async function () {
      const res = await this.$http.get(this.$http.API.APPS_ALL)
      if (res.success) {
        this.appsData = res.data
      }
    },
    createAppApi: async function () {
      let params = this.appForm
      const res = await this.$http.post(this.$http.API.APPS_CREATE, params)
      if (res.success) {
        this.$message({
          message: '',
          type: 'success'
        })
        this.appsApi()
      }
    },
    updateAppApi: async function (id) {
      let params = this.appForm
      const res = await this.$http.put(this.$http.API.APPS_UPDATE + id, params)
      if (res.success) {
        this.$message({
          message: '',
          type: 'success'
        })
        this.appsApi()
      }
    },
    deleteAppApi: async function (id) {
      const res = await this.$http.delete(this.$http.API.APPS_DELETE + id)
      if (res.success) {
        this.$message({
          message: '',
          type: 'success'
        })
        this.appsApi()
      }
    }
  },
  created () {
    this.appsApi()
  }
}
</script>
